























































import Vue from "vue";
import Index from "./components/Index.vue";

export default Vue.extend({
  name: "App",

  components: {
    Index
  },

  data: () => ({
    activeBtn: 0
  }),

  methods: {
    goToLink(link: string) {
      try {
        this.$vuetify.goTo(link, {});
      } catch (error) {}
    }
  }
});
